.help {
  padding: 80px 0 45px;

  @include respond-to(phones) {
    padding: 42px 15px 45px;
  }
}

.help__content{
  max-width: 1200px;
  margin: auto;
}

.help__list {
  text-align: center;
  @include respond-to(phones) {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 30px;
  }
}

.help__item{
  margin: 0 57px 55px;
  display: inline-block;

  @include respond-to(laptops) {
    width: 170px;
    margin: 0 28px 55px;
  }

  @include respond-to(phones) {
    width: 130px;
    margin: auto;
  }

  &:nth-child(1) .help__icon{
    background-image: url("../assets/icons/icon-pet-food.png");
  }

  &:nth-child(2) .help__icon{
    background-image: url("../assets/icons/icon-transportation.png");
  }

  &:nth-child(3) .help__icon{
    background-image: url("../assets/icons/icon-toys.png");
  }

  &:nth-child(4) .help__icon{
    background-image: url("../assets/icons/icon-bowls-and-cups.png");
  }

  &:nth-child(5) .help__icon{
    background-image: url("../assets/icons/icon-shampoos.png");
  }

  &:nth-child(6) .help__icon{
    background-image: url("../assets/icons/icon-vitamins.png");
  }

  &:nth-child(7) .help__icon{
    background-image: url("../assets/icons/icon-medicines.png");
  }

  &:nth-child(8) .help__icon{
    background-image: url("../assets/icons/icon-collars-leashes.png");
  }

  &:nth-child(9) .help__icon{
    background-image: url("../assets/icons/icon-sleeping-area.png");
  }
}

.help__icon{
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 60px;
  height: 60px;
  margin: 0 auto 30px;

  @include respond-to(phones){
    width: 50px;
    height: 50px;
    margin-bottom: 20px;
  }
}

.help__title{
  @include georgia-normal();
  font-size: 20px;
  line-height: 115%;
  text-align: center;
  letter-spacing: 0.06em;
  color: $color-title-dark;

  @include respond-to(phones){
    font-size: 15px;
    line-height: 110%;
    letter-spacing: 0.06em;
  }
}