.first-screen {
  background: url("../assets/images/noise-lines.png"), radial-gradient(100% 215.42% at 0% 0%, #5B483A 0%, #262425 100%), #211F20;
}

.first-screen__content {
  max-width: 1200px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin: auto;
  padding-top: 180px;
  @include respond-to(laptops) {
    flex-direction: column;
    align-items: center;
    padding-top: 150px;
  }

  @include respond-to(phones) {
    padding-left: 10px;
    padding-right: 10px;
    text-align: center;
  }
}

.first-screen__text{
  padding-top: 163px;

  @include respond-to(laptops) {
    padding-top: 0;
    display: flex;
    flex-direction: column;
  }

  @include respond-to(phones) {
    align-items: center;
  }
}

.first-screen .first-screen__main-title {
  margin-bottom: 42px;
  max-width: 310px;

  @include respond-to(phones) {
    font-size: 25px;
    max-width: 230px;
    letter-spacing: 0.06em;
  }
}

.first-screen__desc {
  max-width: 460px;
  margin-bottom: 41px;
}

.first-screen__button-link {
  margin: auto;
}

.first-screen__img{
  width: 698px;
  height: 728px;
  background-image: url("../assets/images/start-screen-puppy.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: bottom right;

  @include respond-to(laptops) {
    width: 710px;
    height: 593px;
    background-position-y: bottom;
    margin-top: 100px;
    background-size: contain;
  }

  @include respond-to(phones){
    width: 260px;
    height: 272px;
    background-position-x: 20px;
    margin-top: 105px;
  }
}

